/**
 * 注: 此页面只为移动端服务, 在pc模式下会导致页面失去比例
 * 招商h5页
 */

import { getProgressList } from "~/api/agent";
import loading from "~/components/loading";
export default {
  components: {
    loading,
  },
  data() {
    return {
      top_img: "",
      dataList: [], // 数据列表
      frist: {
        main_title: '',
        
      }, // 第一个显示
      loading: false,
      page: 1,
      limit: 10,
      agent_id: "", // 代理商id
    };
  },

  mounted() {
    // 获取所有的省份
    if (process.client) {
      document.title = "扶农进展";
    }

    this.agent_id = this.$route.query.agent_id || this.$route.query.agent_now_id;

    this.getData();

    if (process.client) {
      window.onscroll = this.onPageScroll;
    }
  },
  destroyed() {
    if (process.client) {
      window.onscroll = null;
    }
  },
  methods: {
    // 获取页面数据
    getData() {
      this.loading = true;
      getProgressList({
        agent_id: this.agent_id || -1 ,
        page: this.page,
        limit: this.limit
      }).then((res) => {
        this.loading = false;
        if (res.data.code === 1000) {
          if (this.page === 1) {
            this.frist = res.data.data.list[0];
            this.dataList = res.data.data.list.splice(1, res.data.data.list.length)
            }else{
            this.dataList = this.dataList.concat(res.data.data.list);
          }
          this.top_img = res.data.data.top_img;
          if (res.data.data.list.length > 0) {
            this.page = this.page + 1
          }
        }
      });
    },
    onPageScroll() {
      if (this.loading) return;
      //为了保证兼容性，这里取两个值，哪个有值取哪一个
      //scrollTop就是触发滚轮事件时滚轮的高度

      if (this.throttleKey) {
        return;
      }
      this.throttleKey = true;

      setTimeout(() => {
        this.throttleKey = false;
        clearTimeout(this.throttleKey);
      }, 50);
      let scrollTop =
        (document.documentElement.scrollTop || document.body.scrollTop) +
        document.documentElement.clientHeight;
      let pageHeight = document.documentElement.scrollHeight;

      /**保证是向下滑动 */
      if (pageHeight - 50 <= scrollTop && scrollTop > this.scrollTop) {
        this.getData();
      }
      this.scrollTop = scrollTop;
    },
    handleItemInfo(id) {
      this.$router.push("/progress_detail?id=" + id);
    },
  },
};
